@import url('https://fonts.googleapis.com/css2?family=Gitan+Latin:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Shuttle+Block&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Venice+Blvd&display=swap');
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Make sure the video stays behind other content */
  overflow: hidden;
}
.background-video {
  object-fit: cover; /* Ensure the video covers the whole screen */
  width: 100%;
  height: 100%;
}
.App-header {
  background-color: #e100ff;
  min-height: 100vh;
  display: flex;
  flex-direction: center;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: -1px;
}

.App-link {
  color: #61dafb;
}.home-page-content {
  display: flex;
  flex-direction: column; /* Stack elements vertically */
  align-items: center; /* Align all items in the center horizontally */
  padding-top: 20px; /* Adjust padding to space out the top */
}

.description {
  text-align: center;
  margin-bottom: 5px; 
  z-index: 50;
  background-color: rgb(84, 0, 85);
  border-radius: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: 2px solid #3498db; /* Border for the description */
  outline: 4px solid #fffdfd; /* Outer outline around the border */
   /* Slight shadow for depth */
  /* Rounded corners */


}

.profile-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px; /* Space between profile and buttons */
}

.profile-picture {
  margin-top: 250px; 
  width: 150px; /* Adjust the width of the profile picture */
  height: 150px; /* Adjust the height of the profile picture */
  border-radius: 50%; /* Make the image circular */
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 15px; /* Space between buttons */
}

.header-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.header-button:hover {
  background-color: #555; /* Hover effect for buttons */
}
.bottom-nav-buttons {
  width: 100px; /* Set width of the circle */
  height: 100px; /* Set height of the circle */
  background: linear-gradient(to bottom, #6a0dad, #7b015b);
  border-radius: 50%; /* Make the box circular */
  border: 2px solid white; /* White border outline */
  display: flex; /* Use flexbox for center alignment of content */
  justify-content: center; /* Horizontally center content inside the box */
  align-items: center; /* Vertically center content inside the box */
  color: white; /* Text color inside the box */
  font-family: 'Shuttle Block', sans-serif;
  font-weight: 300;
  font:bold;
  font-size: 14px; /* Optional: adjust font size */
}

.bottom-nav-buttons-container {
  display: flex; /* Use flexbox to align the boxes horizontally */
  justify-content: space-evenly; /* Space the boxes evenly horizontally */
  align-items: center; /* Align items vertically */
  padding: 20px; /* Optional: adds padding around the container */
  z-index: 200;
}

/* Hover effect for the buttons */
.bottom-nav-buttons:hover {
  background-color: #aa29b9; /* Darker blue on hover */
  transform: scale(1.1); /* Slight scale effect on hover */
}

/* Active effect when button is clicked */
.bottom-nav-buttons:active {
  background-color: #861f91; /* Even darker blue when active */
  transform: scale(0.95); /* Slightly shrink the button when clicked */
}
.vector {
  position: absolute;
  top: 190px; /* Distance from the bottom of the screen */
  right: 0px;  /* Distance from the right edge of the screen */
  width: 900px;
  height: auto; /* Maintain aspect ratio */
  z-index: 0; /* Ensure it stays on top of other elements */
}
.vector2{
  position: absolute; /* Position the image inside the section */
  top: 190px; /* Position it near the bottom */
  left: 0px; /* Position it near the right */
  width: 900px; /* Control the image width */
  height: auto; /* Maintain aspect ratio */
  max-width: 600%; /* Ensure image doesn't overflow horizontally */
  max-height: 600%; /* Ensure image doesn't overflow vertically */
  object-fit: contain; /* Make the image scale proportionally */
  z-index: 0; /* Keep the image on top of other elements */
}
.navboxes{
  width: 100px; /* Set width of the circle */
  height: 100px; /* Set height of the circle */
  background-color: #1d0031; /* Dark purple background color */
  border-radius: 50%; /* Make the box circular */
  border: 2px solid white; /* White border outline */
  display: flex; /* Use flexbox for center alignment of content */
  justify-content: space-evenly; /* Horizontally center content inside the box */
  align-items: center; /* Vertically center content inside the box */
  color: white; /* Text color inside the box */
  font-size: 18px; /* Optional: adjust font size */
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
