/* Portfolio.css */

.header {
    width: 100%;
    padding: 20px ;
    background: linear-gradient(to bottom, #6a0dad, #ff69b4);
    display: flex;
    flex-direction: column; /* Stack title above nav */
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: absolute;
    outline-color: #000000;
    outline-width: 200pt;
    top: 0;
    z-index: 50;
  }

  
  /* Centering Content */
  .header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;

    z-index: 50;
  
  }
.portfolio-page {
    margin-bottom: -1800px;
    padding: 20px;
    z-index: 100;
    color: rgb(255, 255, 255);

  }
  
  .projects-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .project-item {
    flex: 1 1 calc(33.333% - 20px); /* Adjust the width of each project item */
    text-align: center;
    margin-bottom: px;
    color: rgb(255, 255, 255);
    padding: 10px 20px;
    background-color: #242424;
    z-index: 1000;

  }
  
  .project-image {
    width: 100%;
    max-width: 200px; /* Adjust the max width of the image */
    cursor: pointer;
    z-index: 50;
  }
  .project-thumbnail:hover {
    transform: scale(1.1); /* Slight zoom effect */
  }
  .overlay-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8); /* Dark grey background with transparency */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10000;
  }
  
  .project-image, .file-thumbnail {
    width: 150px; /* Adjust size as needed */
    height: auto;
    display: block;
    margin: 10px auto;
  }
  .file-thumbnail {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 5px;
  }
  .project-thumbnail {
    width: 150px;
    height: auto;
    cursor: pointer;
    border: 2px solid #ddd;
    border-radius: 8px;
    transition: transform 0.2s;
  }
  
  .image-modal img {
    max-width: 90%;
    max-height: 90%;
    margin: auto;
    z-index: 1000;

  }
  
  .download-button {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
    text-decoration: none;
    margin-top: 20px;

  }
/* Style for the title box (legend) */
/* Style for the title box (legend) as a wide rectangle */
.legend-download {
    display: flex; /* Ensures content aligns properly */
    justify-content: center; /* Center text horizontally */
    align-items: center; /* Center text vertically */
    background: rgba(0, 0, 0, 0.8); /* Button background color */
    color: white; /* Text color */
    font-size: 14px; /* Text size */
    text-align: center;
    padding: 5px 0; /* Minimal height with vertical padding */
    width: 70%; /* Adjust width as needed */
    margin-bottom: 40px; /* Center the button horizontally */
    margin-left: 90px;
    border-radius: 4px; /* Rounded corners for a modern look */
    cursor: pointer; /* Pointer cursor to indicate interactivity */
    text-decoration: none; /* Remove underline from link */
    transition: background 0.3s ease; /* Smooth hover transition */
    z-index: 300;
  }
  
  /* Hover effect */
  .legend-download:hover {
    background: rgba(0, 144, 10, 0.9); /* Slightly darker on hover */
    margin-bottom: 30px; /* Center the button horizontally */

  }
  
  /* Ensures proper height and alignment for legends that are not clickable */
  .legend {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    text-align: center;
    padding: 5px 0;
    width: 90%;
    margin-top: 70px;
    background: rgba(0, 0, 0, 0.5); /* Non-clickable background */
    color: white;
    border-radius: 4px;
  }
  
  
  .download-button:hover {
    margin-top:20px;
    background-color: #45a049;
  }
  
  /* Modal styling for enlarging images */
  .image-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 1000;
    bottom: 1000;
    background-color: rgba(255, 255, 255, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100000;
    cursor: pointer;
  }
  .modal-content {
    background: white;
    border-radius: 8px;
    padding: 20px;
    width: 80%;
    max-width: 1200px;
    height: 80%;
    max-height: 800px;
    overflow: hidden;
    position: relative;
    z-index: 100000000; /* Ensure it's above the overlay */
  }
  .image-modal:hover {
    cursor: pointer;
  }
  .enlarged-image {
    max-width: 100%;
    max-height: 100%;
    border-radius: 5px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.3s ease-in-out;
    object-fit: contain;
    margin: auto;
    z-index: 2000;
  }
  .vector1 {
    position: absolute;
    margin-top: 1720px;
    top: -40px; /* Distance from the bottom of the screen */
    margin-right: -120px;  /* Distance from the right edge of the screen */
    width: 1300px;
    max-width: 500%;
    height: auto; /* Maintain aspect ratio */
    object-fit: contain; /* Make the image scale proportionally */
    z-index: 2; /* Ensure it stays on top of other elements */
  }
  .vector3{
    position: absolute; /* Position the image inside the section */
    top: 187px; /* Position it near the bottom */
    left: 0px; /* Position it near the right */
    width: 1300px; /* Control the image width */
    height: auto; /* Maintain aspect ratio */
    max-width: 600%; /* Ensure image doesn't overflow horizontally */
    max-height: 600%; /* Ensure image doesn't overflow vertically */
    object-fit: contain; /* Make the image scale proportionally */
    z-index: -2; /* Keep the image on top of other elements */
  }
  .portfolio-page-vector2{
    position: absolute; /* Position the image inside the section */
    top: 190px; /* Position it near the bottom */
    left: 0px; /* Position it near the right */
    width: 1200px; /* Control the image width */
    height: auto; /* Maintain aspect ratio */
    max-width: 600%; /* Ensure image doesn't overflow horizontally */
    max-height: 600%; /* Ensure image doesn't overflow vertically */
    object-fit: contain; /* Make the image scale proportionally */
    z-index: 1; /* Keep the image on top of other elements */
  } 
  .portfolio-page h1 {
    border: 2px solid #ff7ee9; /* Add border */
    padding: 10px; /* Padding around the title */
    border-radius: 5px; /* Optional rounded corners */
    text-shadow: 2px 2px 5px rgba(63, 0, 52, 0.7); /* Optional glowing effect */
    color: #ffffff; /* Change the text color */
    
  }

  .portfolio-page {
    background: linear-gradient(180deg, #eba4ff, #550451); /* Light gray gradient */
    border: 5px solid white;
    background-size: cover; /* Ensures the background covers the container */
    background-repeat: no-repeat; /* Prevents tiling */
    background-position: center; /* Centers the image */
    background-attachment: fixed; /* Makes the background adjust to window resizing */
    min-height: 400vh;
    min-width: 220vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #ffffff;
    outline: #000000;
    outline-width: 10px;
    padding: 50px;
    margin-top: 50px;
    box-sizing: border-box;
    border-radius: 10px;
    z-index: 3000;
  }
  .file-viewer {
    width: 100%; /* Use full width of the modal */
    height: 100%; /* Use full height of the modal */
    border: none; /* Remove default iframe border */
  }
  
  /* Close button */
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: red;
    color: white;
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    cursor: pointer;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10002; /* Even higher than the modal content */
  }
  .carousel-container {
    max-width: 600px; /* Set desired width */
    margin: 0 auto; /* Center the carousel */
    z-index: 2000;
  }
  .carousel .carousel-slider {
    max-height: 400px; /* Limit the height */
    z-index: 0;
  }
  .custom-carousel {
    object-fit: contain; /* Scale the image to fit within the container */
    max-height: 800px; /* Limit height */
    width: autopx; /* Maintain aspect ratio */
    z-index: 300;
  }
  html, body {
    height: 100%; /* Make sure html and body cover the full height */
  }
  
 
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.9);
      z-index: 2000;

    }
    to {
      opacity: 1;
      transform: scale(1);
      z-index: 2000;

    }
  }
  